/* media querry */
@media only screen and (max-width: 600px) {

    
       #menu{
        position: fixed;
        top: 0px;
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        align-items: flex-start;
        justify-content: flex-start;
        position:absolute;
        width: 100%;
        height: 100%;
        background: linear-gradient(to right,  #EAEDED 0%,#EAEDED 90%,transparent 90%);
        z-index: 100;
        position: absolute;
        top: 0;
        color: #000;
        font-size:n25px;
       
       }
      .menuicon1{
        visibility: visible;
        color: #ffffff;
      }
      .logo_menu{
        display: flex;
        justify-content: center;
        background-color: #131921;
      }
      .returns ,.location, .countries,.hidesearch,.dep2{
        display: none;
      }
      .logo_menu2{
        /* display: flex; */
        background-color: #131921;
        width: 90%;
        height: 80px;
        align-items: flex-start;
        margin-top: 10px;
      }
      .dep{
        margin-top: 10px;
        background-color: #ffffff;
        width: 90%;
      }
      .returns2, .location2,.cart2,.login-singin2{
        display: flex;
        background-color: #ffffff;
        width: 90%;
        height: 80px;
        justify-content:center;
        align-items: center;
        margin-top: 10px;
      }
      .location2{
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        
        align-items: center;
        grid-gap: 10px;
        gap: 10px;
      }
      .nav-container{
        margin: 0;
        height: 90px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
      }
      .search{
        position: absolute;
        top: 50px;
        margin-right: 20px;
        width: 100%;
        z-index: 1;
      }
   .cart{
   margin-top: -9px;
   }
}


